.root {
  li {
    display: inline-block;

    a {
      text-align: center;
      width: 45px;
      line-height: 42px;
      height: 45px;
      border: 1px solid #fe5f1e;
      border-radius: 30px;
      margin-right: 10px;
      cursor: pointer;
      display: inline-block;
      color: #fe5f1e;
      transition: all 0.2s ease-in-out;

      &:hover {
        background-color: #fe5f1e;
        color: #fff;
      }
    }
  }

  :global {
    .selected {
      a {
        background-color: #fe5f1e;
        color: #fff;
      }
    }

    .disabled {
      a {
        color: #b4b4b4;
        border: 1px solid #b4b4b4;

        &:hover {
          background-color: #b4b4b4
        }
      }
    }
  }
}